.formContainer{
    height: fit-content;
    min-height: 20rem;
    width: 40vw;
    border: 1px solid black;
    position: relative;
    margin: auto;
    top: 7rem;
    padding: 1rem;

}

.addBtnContainer{
    text-align: end;
    margin-right: 5rem;
}

.addBtn{
    height: 2rem;
    width: 5rem;
    border: 1px solid black;
    border-radius: 5px;
    position: relative;
    display: inline-block;
    background-color: white;
    text-align: center;
    cursor: pointer;
}

.addFormContainer{
    margin-top: 1rem;
    display: flex;
}

.inputField{
    flex: 60%;
    margin-right: 1rem;
    border-radius: 5px;
}

.saveBtn{
    height: 1.8rem;
    width: 4rem;
    background-color: rgb(224, 42, 42);
    color: white;
    border-radius: 5px;
    position: relative;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
}

.dataValueContainer{
    margin-top: 1rem;
    display: flex;
    height: 2rem;
}

.dataInput{
    width: 85%;
    margin-right: 1rem;
}

.fontBtn{
    position: relative;
    margin: 5px;
    top: 3px;
    cursor: pointer;
}